// Additional colors
$texture-colors: (
        "gold": #ffcf21,
        "silver": #ffffff,
        "copper": #ff5100) !default;

$textures: (
        "metal": url('data:image/jpeg;base64,')
) !default;
$textures-radial: (
        "metal": url('data:image/jpeg;base64,')
) !default;

:root {
  // Defining each --texture-[NAME] css variable
  @each $name, $value in $textures {
    --texture-#{$name}: #{$value};
  }
  // Defining each --texture-radial-[NAME] css variable
  @each $name, $value in $textures-radial {
    --texture-radial-#{$name}: #{$value};
  }
  // Defining each --texture-color-[NAME] css variable
  @each $color, $value in $texture-colors {
    --#{$color}: #{$value};
  }
}

  $theme-colors: (
          "primary": #007BFF,
          "secondary": #868E96,
          "success": #0B4D1F,
          "info": #0B374D,
          "warning": #EDB637,
          "danger": #9B000F,
          "light": #F8F9FA,
          "dark": #343A40
  ) !default;

$texture-colors: map-merge($theme-colors, $texture-colors);

// Levels of shinyness
$shinyness: (0, 1, 2, 3, 4, 5, 6, 7);

@mixin texture-background($texture, $color, $shiny){
  background-repeat: repeat;
  background-blend-mode: luminosity,multiply;
  background-size: 200% 100%, auto, auto;
  @if ($shiny == 1) {
    background-image: linear-gradient(135deg, rgba(0,0,0,.1) 6%, rgba(255,255,255,.35) 15%,rgba(0,0,0,.1) 25%,rgba(0,0,0,.1) 47%), var(--texture-#{$texture}), linear-gradient(var(--#{$color}),var(--#{$color}));
  } @else if ($shiny == 2) {
    background-image: linear-gradient(135deg, rgba(0,0,0,.1) 7%, rgba(255,255,255,.4) 15%, rgba(255,255,255,.0) 17%,rgba(0,0,0,.1) 25%,rgba(255,255,255,.4) 36%, rgba(0,0,0,.0) 40%,rgba(0,0,0,.1) 47%), var(--texture-#{$texture}), linear-gradient(var(--#{$color}),var(--#{$color}));
  } @else if ($shiny == 3) {
    background-image: linear-gradient(135deg, rgba(0,0,0,.1) 5%, rgba(255,255,255,.4) 25%, rgba(0,0,0,.05) 30%,rgba(0,0,0,.1) 47%), var(--texture-#{$texture}), linear-gradient(var(--#{$color}),var(--#{$color}));
  } @else if ($shiny == 4) {
    background-image: linear-gradient(135deg, rgba(0,0,0,.1) 5%, rgba(255,255,255,.3) 41%, rgba(0,0,0,.1) 41.2%,rgba(0,0,0,.1) 47%), var(--texture-#{$texture}), linear-gradient(var(--#{$color}),var(--#{$color}));
  } @else if ($shiny == 5) {
    background-image: linear-gradient(135deg, rgba(0,0,0,.1) 5%, rgba(255,255,255,.3) 13%, rgba(0,0,0,.1) 13.2%,rgba(0,0,0,.1) 47%), var(--texture-#{$texture}), linear-gradient(var(--#{$color}),var(--#{$color}));
  } @else if ($shiny == 6) {
    background-image: linear-gradient(180deg, rgba(0,0,0,.1) 0%, rgba(255,255,255,.3) 47%, rgba(0,0,0,.1) 52%,rgba(255,255,255,.1) 100%), var(--texture-#{$texture}), linear-gradient(var(--#{$color}),var(--#{$color}));
  } @else if ($shiny == 7) {
    background-image: linear-gradient(25deg, rgba(0,0,0,.1) 14%, rgba(255,255,255,.3) 35%, rgba(0,0,0,.1) 45.2%,rgba(0, 0, 0, 0.1) 47%), var(--texture-#{$texture}), linear-gradient(var(--#{$color}),var(--#{$color}));
  } @else {
    background-image: linear-gradient(135deg, rgba(0,0,0,.1) 6%, rgba(255,255,255,.2) 15%,rgba(0,0,0,.1) 25%,rgba(0,0,0,.1) 45%), var(--texture-#{$texture}), linear-gradient(var(--#{$color}),var(--#{$color}));
  }

}

