@each $texture-name, $texture-value in $textures {
  @each $color-name, $color-value in $texture-colors {
    // Generic
    .bg-#{$texture-name}.bg-#{$color-name} {
      color: rgba(255, 255, 255, 1);
      background-repeat: repeat;
      @include texture-background($texture-name, $color-name, false);
    }
    // Buttons
    .bg-#{$texture-name}.btn-#{$color-name} {
      color: rgba(255, 255, 255, 1);
      background-repeat: repeat;
      @include texture-background($texture-name, $color-name, false);
    }
    // Foreground Text
    /*.text-#{$texture-name}.text-#{$color-name} {
      background-clip: text;
      color: rgba(0, 0, 0, 0);
      @include texture-background($texture-name, $color-name, false);
      // TODO shinyness (see backup)
    }*/

    .text-#{$texture-name}.text-#{$color-name} {
      @supports (mix-blend-mode: lighten) {
        display: inline-block;
        position: relative;

        &::before {
          content: '';
          display: block;
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;

          @include texture-background($texture-name, $color-name, false);
          pointer-events: none;
        }
/*
        color: #000;
        background: #fff;
        mix-blend-mode: multiply;

        &::before {
          mix-blend-mode: screen;
        }*/
        color: #fff;
        background: #000;
        mix-blend-mode: lighten;

        &::before {
          mix-blend-mode: multiply;
        }
      }
    }
  }
}
.metallic-anim {
animation: metallic-animation 2s linear infinite;
}
.metallic-anim-2 {
animation: metallic-animation-2 2s linear infinite;
}

@keyframes metallic-animation {
from { background-position: 0 0, 0 0, 0 0; }
to { background-position: -200% 0, 0 0, 0 0; }
}
@keyframes metallic-animation-2 {
from { background-position: 0 0, 0 0, 0 0; }
to { background-position: 200% 0, 0 0, 0 0; }
}
@keyframes metallic-animation-texture-1 {
from { background-position: 0 0, 0 0, 0 0; }
to { background-position: 0 0, 64px 0, 0 0; }
}

